<template>
    <div class="wrapB" style="padding-top: 100px;">
        <h1 class="title" style="padding-bottom: 1em; font-weight : 600">
            
        </h1>
        <div class="join">
            <div class="input-with-label">
                <input v-model="nickName" id="nickname" placeholder=" ." type="text" />
                <label for="nickname"></label>
            </div>

            <div class="input-with-label">
                <input
                    v-model="email"
                    v-bind:class="{
                        error: error.email,
                        complete: !error.email && email.length !== 0
                    }"
                    id="email"
                    placeholder=" ."
                    type="text"
                />
                <label for="email"></label>
                <div class="error-text" v-if="error.email">
                    {{ error.email }}
                </div>
            </div>

            <div class="input-with-label">
                <input
                    v-model="password"
                    v-bind:class="{
                        error: error.password,
                        complete: !error.password && password.length !== 0
                    }"
                    id="password"
                    :type="passwordType"
                    placeholder=" ."
                />
                <label for="password"></label>
                <div class="error-text" v-if="error.password">
                    {{ error.password }}
                </div>
            </div>

            <div class="input-with-label">
                <input
                    v-model="passwordConfirm"
                    :type="passwordConfirmType"
                    id="password-confirm"
                    v-bind:class="{
                        error: error.passwordConfirm,
                        complete: !error.passwordConfirm && passwordConfirm.length != 0
                    }"
                    placeholder="  ."
                />
                <label for="password-confirm"> </label>
                <div class="error-text" v-if="error.passwordConfirm">
                    {{ error.passwordConfirm }}
                </div>
            </div>
        </div>

        <label>
            <input
                v-model="isTerm"
                type="checkbox"
                id="term"
                v-bind:class="{
                    error: error.isTerm,
                    complete: !error.isTerm
                }"
            />
            <span> .</span>
        </label>
        <div v-if="showModal">
            <transition name="modal">
                <div class="modal-mask">
                    <div class="modal-wrapper">
                        <div class="modal-container">
                            <div class="modal-header">
                                <slot name="header">
                                    
                                </slot>
                            </div>
                            <div class="modal-body"></div>
                            <div class="modal-footer">
                                <slot name="footer">
                                    ?<br />
                                    <button @click="showmodal"></button>
                                </slot>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
        </div>
        <button @click="showModal = true"></button>

        <button class="btn btn--back" v-on:click="join" :disabled="!isSubmit" :class="{ disabled: !isSubmit }">
            
        </button>
        <button class="btn btn--back" v-on:click="back" style="margin-top:10px">
             
        </button>
    </div>
</template>

<script>
import PV from 'password-validator';
import * as EmailValidator from 'email-validator';
//import UserApi from '../../apis/UserApi';
export default {
    data: () => {
        return {
            email: '',
            password: '',
            passwordSchema: new PV(),
            passwordConfirm: '',
            nickName: '',
            isTerm: false,
            isLoading: false,
            error: {
                email: false,
                password: false,
                nickName: false,
                passwordConfirm: false,
                isTerm: false
            },
            isSubmit: false,
            passwordType: 'password',
            passwordConfirmType: 'password',
            showModal: false
        };
    },
    created() {
        this.passwordSchema
            .is()
            .min(8)
            .is()
            .max(100)
            .has()
            .digits()
            .has()
            .letters();
    },
    watch: {
        password: function() {
            this.checkForm();
        },
        email: function() {
            this.checkForm();
        },
        passwordConfirm: function() {
            this.checkForm();
        },
        isTerm: function() {
            this.checkForm();
        }
    },
    methods: {
        checkForm() {
            if (this.email.length >= 0 && !EmailValidator.validate(this.email)) this.error.email = '  .';
            else this.error.email = false;

            if (this.password.length >= 0 && !this.passwordSchema.validate(this.password)) this.error.password = ',  8  .';
            else this.error.password = false;

            if (this.password != this.passwordConfirm) this.error.passwordConfirm = '  .';
            else this.error.passwordConfirm = false;

            if (this.isTerm == false) this.error.isTerm = true;
            else this.error.isTerm = false;
            let isSubmit = true;
            Object.values(this.error).map(v => {
                if (v) isSubmit = false;
            });
            this.isSubmit = isSubmit;
        },
        join() {
            if (this.isSubmit) {
                let { email, password, nickName } = this;
                // eslint-disable-next-line no-unused-vars
                let data = {
                    email,
                    password,
                    nickName
                };

                //   
                this.isSubmit = false;

                this.$router.push('/user/certification');
                // UserApi.requestLogin(data,res=>{
                //     //     
                //     console.log(res);
                //
                //     //   
                //     this.isSubmit = true;
                // },error=>{
                //     //   
                //     this.isSubmit = true;
                // })
            }
        },
        back() {
            this.$router.push('/');
        },
        showmodal() {
            this.showModal = false;
        }
    }
};
</script>
