<template>
    <div class="user" id="login">
        <div class="wrapC">
            <img src="../../assets/images/paper-plane.png" style="display:block; margin: 0px auto" width="150" height="150" />
            <h1>
                  
                <br />  .
            </h1>
            <div class="input-with-label">
                <input
                    v-model="email"
                    v-bind:class="{
                        error: error.email,
                        complete: !error.email && email.length !== 0
                    }"
                    @keyup.enter="login"
                    id="email"
                    placeholder=" ."
                    type="text"
                />
                <label for="email"></label>
                <div class="error-text" v-if="error.email">{{ error.email }}</div>
            </div>

            <div class="input-with-label">
                <input
                    v-model="password"
                    type="password"
                    v-bind:class="{
                        error: error.password,
                        complete: !error.password && password.length !== 0
                    }"
                    id="password"
                    @keyup.enter="login"
                    placeholder=" ."
                />
                <label for="password"></label>
                <div class="error-text" v-if="error.password">{{ error.password }}</div>
            </div>
            <button class="btn btn--back btn--login" v-on:click="login" :disabled="!isSubmit" :class="{ disabled: !isSubmit }"></button>

            <div class="sns-login">
                <div class="text">
                    <p>SNS  </p>
                    <div class="bar"></div>
                </div>
                <kakaoLogin :component="component" />
                <GoogleLogin :component="component" />
                <TwitterLogin :component="component" />
                <FacebookLogin :component="component" />
            </div>
            <div class="add-option">
                <div class="text">
                    <p></p>
                    <div class="bar"></div>
                </div>
                <div class="wrap">
                    <p> ?</p>
                    <router-link v-bind:to="{ name: 'Find' }" class="btn--text"> </router-link>
                </div>
                <div class="wrap">
                    <p>  ?</p>
                    <router-link v-bind:to="{ name: 'Join' }" class="btn--text"></router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '../../assets/css/style.scss';
import '../../assets/css/user.scss';
import PV from 'password-validator';
import * as EmailValidator from 'email-validator';
import KakaoLogin from '../../components/user/snsLogin/Kakao.vue';
import GoogleLogin from '../../components/user/snsLogin/Google.vue';
import TwitterLogin from '../../components/user/snsLogin/Twitter.vue';
import FacebookLogin from '../../components/user/snsLogin/Facebook.vue';
import UserApi from '../../apis/UserApi';

export default {
    components: {
        KakaoLogin,
        GoogleLogin,
        FacebookLogin,
        TwitterLogin
    },
    created() {
        this.component = this;

        this.passwordSchema
            .is()
            .min(8)
            .is()
            .max(100)
            .has()
            .digits()
            .has()
            .letters();
    },
    watch: {
        password: function() {
            this.checkForm();
        },
        email: function() {
            this.checkForm();
        }
    },
    methods: {
        checkForm() {
            if (this.email.length >= 0 && !EmailValidator.validate(this.email)) this.error.email = '  .';
            else this.error.email = false;

            if (this.password.length >= 0 && !this.passwordSchema.validate(this.password)) this.error.password = ',  8  .';
            else this.error.password = false;

            let isSubmit = true;
            Object.values(this.error).map(v => {
                if (v) isSubmit = false;
            });
            this.isSubmit = isSubmit;
        },
        login() {
            if (this.isSubmit) {
                let { email, password } = this;
                let data = {
                    email,
                    password
                };

                //   
                this.isSubmit = false;
                this.$router.push('/user/complete');
                UserApi.requestLogin(
                    data,
                    res => {
                        //     
                        console.log(res);

                        //   
                        this.isSubmit = true;
                    },
                    // eslint-disable-next-line no-unused-vars
                    error => {
                        //   
                        this.isSubmit = true;
                    }
                );
            }
        }
    },
    data: () => {
        return {
            email: '',
            password: '',
            passwordSchema: new PV(),
            error: {
                email: false,
                passowrd: false
            },
            isSubmit: false,
            component: this
        };
    }
};
</script>
